import { csv } from "d3";
import { Month, MonthMap } from "../consts";
import { DailyChange, KeyDate, RawYearComparison, RawYearMarkets, YoYMarket, YoYMarketRegion, YoYRegion, YoYTotal } from "./DataTypes";

export async function getYoYTotalData(setFunction: Function) {
    csv(`${process.env.PUBLIC_URL}/YoYTotal.csv`).then(function (data: any): void {
        let tmpData: YoYTotal[] = [];
        data.forEach((item: any) => {
            let currentItem: YoYTotal = {
                Year: item["Year"],
                Month: new Date(item["Year"], MonthMap[item["Month"] as Month], 1),
                YoYChange: item["ConsumptionYoY"]
            }
            tmpData.push(currentItem)
        })
        setFunction(tmpData)
    }).catch(function (err: any) { throw err; })
}

