export async function getDailyChangeData(setFunction: Function) {
    csv(`${process.env.PUBLIC_URL}/YoyByDay.csv`).then(function (data: any): void {
        let tmpData: DailyChange[] = [];
        data.forEach((item: any) => {
            let parts = item["Date"].split('/');
            let currentItem: DailyChange = {
                Date: new Date(parts[2], parts[1] - 1, parts[0]),
                Change: item["ConsumptionChange"],
            }
            tmpData.push(currentItem)
        })
        setFunction(tmpData)
    }).catch(function (err: any) { throw err; })
}

export async function getKeyDateData(setFunction: Function) {
    csv(`${process.env.PUBLIC_URL}/KeyDateData.csv`).then(function (data: any): void {
        let tmpData: KeyDate[] = [];
        data.forEach((item: any) => {
            let currentItem: KeyDate = {
                Date: new Date(item["Earliest Date"]),
                DayName: item["Event"],
                Consumption: item["Consumption (kWh)"],
                Year: parseInt(item["Calendar Year"]),
                Market: item["Market Type"]
            }
            tmpData.push(currentItem)
        })
        setFunction(tmpData)
    }).catch(function (err: any) { throw err; })
}